:root{font-family:'Comfortaa', cursive;background-color:#000;color:#fff;font-size:16px;scroll-behavior:smooth}h1,h2{text-transform:lowercase}p{font-family:'Open Sans', sans-serif}header{width:98vw;height:90vh}header #header-graphic{display:block;width:100%}header h1{margin:0;font-weight:bold;font-size:3rem}footer{display:block;position:fixed;bottom:0;z-index:2;width:100%;padding:1rem;font-size:0.8rem;border-top:solid 1px white;background-color:rgba(0,0,0,0.9)}nav{display:block;position:fixed;top:0;z-index:2;width:100%;padding:0.5rem;background-color:rgba(0,0,0,0.7)}nav ul{margin:0;padding:0}nav li{list-style:none;display:inline-block}nav a{padding:0 2rem 0 0;text-decoration:none;font-size:1.5rem;font-weight:bold;color:white;transition:250ms}nav a:hover{color:#00B8DF}nav .page-links{display:flex;flex-direction:row;flex-wrap:wrap}a.button-link{font-family:'Comfortaa', cursive;font-size:1rem;font-weight:bolder;text-decoration:none;color:#00B8DF;background-color:rgba(0,0,0,0);border:solid #00B8DF 3px;border-radius:0.75em;margin:0.2em;padding:0.5em 1em;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;transition:all 0.2s ease-in-out}a.button-link:hover{background-color:#00B8DF;color:#000}.color{background-color:#fff;width:100%;height:calc(3rem - 5px);border-radius:3rem;margin:0 1rem}.yellow{background-color:#F8DB6C}.blue{background-color:#00B8DF}.red{background-color:#E36799}.row{display:flex;flex-direction:row}.col{display:flex;flex-direction:column}.wrap{flex-wrap:wrap}.center{align-items:center;justify-content:center}#header{min-height:100vh}#about{min-height:100vh;padding:6rem 1rem}#video{min-height:100vh;padding:8em 10em}@media screen and (max-width: 899px){#video{padding:2em;margin:0}}@media screen and (max-width: 667px){#video{padding:5rem 0}}.gallery-container{display:grid;grid-gap:1rem;gap:1rem;justify-items:center;grid-template-columns:repeat(auto-fill, minmax(16em, 1fr))}@media screen and (min-width: 1024px){.gallery-container{grid-template-columns:repeat(3, minmax(16em, 1fr))}}@media screen and (max-width: 899px){.gallery-container{grid-template-columns:repeat(2, minmax(16em, 1fr))}}@media screen and (max-width: 667px){.gallery-container{grid-template-columns:repeat(1, minmax(16em, 1fr))}}.gallery-container .video-container{width:20vw;height:11vw}@media screen and (max-width: 899px){.gallery-container .video-container{width:41vw;height:23vw}}@media screen and (max-width: 667px){.gallery-container .video-container{width:75vw;height:42vw}}#web{padding:3rem 4rem;max-width:100vw}@media screen and (max-width: 667px){#web{padding:5rem 1rem}}.web-container{display:flex;flex-direction:row;position:relative;margin:0 1rem 5rem 1rem;padding:3rem;color:#fff;border-radius:3rem}@media screen and (max-width: 899px){.web-container{flex-direction:column}}@media screen and (max-width: 667px){.web-container{padding:1rem;flex-direction:column}}.web-container .background-shape{position:absolute;top:0;left:0;width:100%;height:100%;background-color:#E36799;border-radius:3rem;-webkit-clip-path:polygon(80% 0, 100% 0, 100% 100%, 40% 100%);clip-path:polygon(80% 0, 100% 0, 100% 100%, 40% 100%);z-index:-1}.web-container .info{flex:1 1;padding:1em;min-width:20rem}@media screen and (max-width: 667px){.web-container .info{min-width:5rem}}.web-container .info .title{font-size:1.5rem;text-transform:uppercase}.web-container .info .quote{font-style:italic;font-size:0.8rem}.web-container .info .job{font-size:1rem;font-style:italic;padding:0}.web-container .info .links{display:flex;flex-direction:inherit;margin:3rem 0}.web-container .images{flex:1 1;padding:1rem}.web-container .images .image-container{position:relative;width:100%;text-align:center}.web-container .images .image-container .desktop{width:30rem;-webkit-filter:drop-shadow(5px 5px 5px rgba(0,0,0,0.7));filter:drop-shadow(5px 5px 5px rgba(0,0,0,0.7))}@media screen and (max-width: 667px){.web-container .images .image-container .desktop{width:20rem}}.web-container .images .image-container .mobile{position:absolute;width:7rem;right:0;bottom:0;-webkit-filter:drop-shadow(5px 5px 5px rgba(0,0,0,0.7));filter:drop-shadow(5px 5px 5px rgba(0,0,0,0.7))}.web-container .images .image-container .mobile-only{width:12rem;-webkit-filter:drop-shadow(5px 5px 5px rgba(0,0,0,0.7));filter:drop-shadow(5px 5px 5px rgba(0,0,0,0.7))}

